
import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './css.css';
export default class SollTab extends Component {
    constructor(props) {
        super(props);

        this.characters = {
            "All Projects": {
                img: require("../../img/All Projects.759c9e28.svg"), color: "LightGrey", text: "black",
                Data: [
                    { title: "React", body: "testwordast", links: "" },
                    { title: "TestReactTitle", body: "一个测试的内容", links: "http://www.baidu.com" }
                ]
            },
            "React": {
                img: require("../../img/React.669f1046.svg"), color: "LightGrey", text: "black",
                Data: [
                    { title: "React", body: "testwordast", links: "" },
                    { title: "TestReactTitle", body: "一个测试的内容", links: "http://www.baidu.com" }
                ]
            },
            "React native": {
                img: require("../../img/React.669f1046.svg"), color: "LightGrey", text: "black",
                Data: [
                    { title: "React native", body: "testwordast", links: "" },
                    { title: "TestReact native", body: "一个测试的内容", links: "" }
                ]
            },
            "Flutter": { img: require("../../img/flutter.png"), color: "LightGrey", text: "black"},
            "Java": { img: require("../../img/java.svg"), color: "LightGrey", text: "black", desc: "\"Just can't seem to miss.\"" },
            "Java script": { img: require("../../img/JavaScript.d0fcd6f7.svg"), color: "LightGrey", text: "black", desc: "\"My armor was never a distraction or a hobby. It was a cocoon. And now I'm a changed man. You can take away my house, all my tricks and toys. But one thing you can't take away... I am Iron Man.\"" },
            "CSS": { img: require("../../img/CSS.svg"), color: "LightGrey", text: "black", desc: "\"I, Loki, Prince of Asgard, Odinson, the rightful King of Jotunheim, God of Mischief, do hereby pledge to you, my undying fidelity.\"" },
            "HTML": { img: require("../../img/html.svg"), color: "LightGrey", text: "black", desc: "\"I, Loki, Prince of Asgard, Odinson, the rightful King of Jotunheim, God of Mischief, do hereby pledge to you, my undying fidelity.\"" },
        };


    }



    render() {
        const tabs = [];
        const tabPanels = [];
        let allData = []//All Projects

        //All Projects的数据由this.characters遍历出Data租成
        Object.keys(this.characters).forEach((name) => {
            if (this.characters[name].Data) {

                allData = allData.concat(this.characters[name].Data)

            }

        })



        Object.keys(this.characters).forEach((name ) => {


            const { img, color: backgroundColor, text: color, Data } = this.characters[name];

            tabs.push(
                <Tab style={{ backgroundColor },{display:"inline-block",marginRight:30,paddingTop:10,paddingBottom:10,cursor:"pointer"}} className="avengers-tab" key={name}>
                    <img src={img} alt={name} height="20" width="20" />
                    <span style={{ verticalAlign: "top", fontWeight: 500,marginLeft:10 }}>{name}</span>
                </Tab>
            );

            name === "All Projects" ?
                

                tabPanels.push(
                    <TabPanel style={{ color }, { display: "flex", flexWrap: "wrap" }} className="avengers-tab-panel" key={'All Projects'}>
                        {/* {desc} */}
                        {
                            allData && allData.map((atem, index) =>
                                <div className="project" style={{ display: "flex", flexDirection: "row", marginRight: "2vw", width: "29vw", marginBottom: "20px" }} key={index}>
                                    <span className="icon" style={{ backgroundColor: "rgb(133, 38, 115)" }, styles.leftBox}>

                                        {atem.title[0]}
                                    </span>
                                    <a style={styles.a} href={atem.links} target="_blanket">
                                        <strong style={{marginRight:10}} data-v-6e499ef8="">
                                            {atem.title}
                                        </strong>
                                        {atem.body}
                                    </a>
                                </div>
                            )

                        }

                    </TabPanel>
                )
                :
                Data && tabPanels.push(
                    <TabPanel style={{ color }, { display: "flex", flexWrap: "wrap" }} className="avengers-tab-panel" key={name}>
                        {/* {desc} */}
                        {
                            Data && Data.map((atem, index) =>
                                <div className="project" style={{ display: "flex", flexDirection: "row", marginRight: "2vw", width: "29vw", marginBottom: "20px" }} key={index}>
                                    <span className="icon" style={{ backgroundColor: "rgb(133, 38, 115)" }, styles.leftBox}>

                                        {atem.title[0]}
                                    </span>
                                    <a style={styles.a} href={atem.links} target="_blanket">
                                        <strong style={{marginRight:10}} data-v-6e499ef8="">
                                            {atem.title}
                                        </strong>
                                        {atem.body}
                                    </a>
                                </div>
                            )

                        }

                    </TabPanel>
                )

        });

        return (
            <div style={{paddingLeft:20,paddingRight:20,height:300}}>
                {/* <p>{links}</p> */}
                <Tabs
                    selectedTabClassName="avengers-tab--selected"
                    selectedTabPanelClassName="avengers-tab-panel--selected"
                >
                    <TabList style={{display:'flex',flexDirection:'row',marginBottom:10,borderWidth:0,borderBottomWidth:1,borderColor:"#d3d3d3",borderStyle:"inset"}}  className="avengers-tab-list ">{tabs}</TabList>
                    {tabPanels}
                </Tabs>
            </div>
        );
    }


}


const styles = {
    a: {
        cursor: "pointer",
        width: "calc(100% - 70px)",
        color: "inherit",
        textDecoration: "none",
        fontWeight: "500",
        lineHeight: "1.5",
        marginLeft: "15px"
    },
    leftBox: {
        width: "40px",
        height: "40px",
    
        lineHeight: "40px",
        textAlign: "center",
        color: "#fff",
        fontWeight: 700,
        fontSize: "1.5rem",
        borderRadius: ".25rem",
        background: "#264e8a",
        display: "block"
    },
    tabheader:{
        flexDirection:'row'
    }
}
