import React, { Component } from 'react'
// connect方法的作用：将额外的props传递给组件，并返回新的组件，组件在该过程中不会受到影响
import { connect } from 'react-redux'
// 引入action
// import { ReactSVG } from 'react-svg'
class CatBigWords extends Component {
    constructor() {
        super()
    }
    render() {
        return (
            <div style={styles.fabox}>

                <p style={{lineHeight:"30px"}}>
                    <font ><font style={{fontSize:19}}>
                        没有人可以回到过去，重新开始；
                        </font></font>
                    <br />
                    <font ><font style={{fontSize:19}}>
                        但谁都可以从现在开始，书写一个全然不同的结局！
                    </font></font>
                </p>
{/* <ReactSVG style={{width:"500px"}} src={"svgCat.svg"} frameborder="0" col = "10" row = "10"></ReactSVG>\ */}
<iframe src={'cat.html'} title="1"  frameBorder="0" col = "10" row = "10" width="800" height="500"></iframe>
            </div>
        )
    }
}




const styles = {
    fabox: {
        // display: "-webkit-box",
        // display: "-ms-flexbox",
        display: "flex",

        justifyContent: "space-around",


        alignItems: "center",
        backgroundColor: "#f8f8f8",
        marginTop: "80px",
        height: "523px",
    },
    inline_block: {
        display: "inline-block"
    },
    name: {

    },
    a: {
        cursor: "pointer",
        padding: "0 2.5rem",
        color: "inherit",
        textDecoration: "none",
        fontWeight: "500",
        lineHeight: "80px",
    }
}




export default connect()(CatBigWords)